.intro {
  display: grid;
  gap: 3.4375rem;
  place-items: center;
  padding-bottom: 2.3125rem;
}
@media (min-width: 40em) {
  .intro {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .intro {
    grid-template-columns: 1fr 1.4fr;
  }
}
.intro__build {
  text-align: center;
}
@media (min-width: 40em) {
  .intro__build {
    text-align: left;
  }
}
.intro__build h1 {
  font-family: var(--font-headings);
  font-size: 1.5625rem;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 0;
  margin-bottom: 1.75rem;
}
@media (min-width: 71.875em) {
  .intro__build h1 {
    margin-bottom: 2.5rem;
    font-size: 2.6875rem;
  }
}
.intro__build p {
  line-height: 1.5;
  margin-bottom: 2.125rem;
}
.intro__build a {
  display: inline-block;
  box-shadow: 0 0.3125rem 0.625rem #c6ccd2;
  border-radius: 7.5rem;
  padding: 0.78125rem 4.09375rem;
  background-color: var(--pink);
  color: var(--white);
  font-size: 0.6875rem;
  font-weight: 700;
  transition: opacity 300ms ease-in;
}
.intro__build a:hover {
  opacity: 0.7;
}
@media (min-width: 71.875em) {
  .intro__build a {
    padding: 1.1625rem 4.34375rem;
    font-size: 0.875rem;
  }
}
.intro__mockup {
  opacity: 0;
  animation: img-appear;
  animation-duration: 2s;
  animation-timing-function: ease-in;
  animation-fill-mode: forwards;
}
.intro__mockup img {
  width: 100%;
  height: 100%;
}